
<template>
    <i-form ref="formLogin" :model="formLogin" :rules="formLoginRules"  class="card-box">
    <Form-item class="formLogin-title">
        <h3>系统登录</h3>
    </Form-item>
        <Form-item prop="username">
            <i-input size="large" type="text" v-model="formLogin.username" placeholder="用户名" @on-enter="handleSubmit('formLogin')">
                <Icon type="ios-person-outline" slot="prepend"></Icon>
            </i-input>
        </Form-item>
        <Form-item prop="password">
            <i-input size="large"  type="password" v-model="formLogin.password" placeholder="密码" @on-enter="handleSubmit('formLogin')">
                <Icon type="ios-lock" slot="prepend"></Icon>
            </i-input>
        </Form-item>
          <Form-item class="login-no-bottom">
            <Checkbox-group v-model="formLogin.remember">
                <Checkbox label="记住密码" name="remember"></Checkbox>
            </Checkbox-group>
        </Form-item>
        <Form-item class="login-no-bottom">
            <Row >
                <i-col :xs="{ span:8, offset: 10 }" >
                    <i-button type="primary" @click="handleSubmit('formLogin')">登录</i-button>
                </i-col>
            </Row>
        </Form-item>
    </i-form>
</template>
<script>
    export default {
        data () {
            return {
                formLogin: {
                    username: '',
                    password: '',
                    remember: []
                },
                formLoginRules: {
                    username: [
                        { required: true, message: '请填写用户名', trigger: 'blur' }
                    ],
                    password: [
                        { required: true, message: '请填写密码', trigger: 'blur' },
                        { type: 'string', min: 5, message: '密码长度不能小于5位', trigger: 'blur' }
                    ]
                }
            }
        },
        methods: {
            handleSubmit(name) {
              let self=this
                this.$refs[name].validate((valid) => {
                    sessionStorage.setItem('user', JSON.stringify(this.formLogin.username.trim()));
                    if (valid) {
                      /*let param = new Object();
                      param["user_phone"] = '+86 ' + this.formLogin.username.trim();
                      let jsonData = protocol.getJsonParamsBeforeLogin(protocol.Login_SendCode, param);
                      http.ajax('', jsonData, false, (data) => {
                        self.$Message.success('提交成功!');
                        self.$router.push({ path: '/adminUser' });
                      })*/

                      if(this.formLogin.username.trim()=="yunwei"){//运维导出页面的展示
                        if(this.formLogin.password.trim()!="yunwei"){
                          self.$Message.success('密码错误!');
                          return
                        }
                        self.$Message.success('提交成功!');
                        self.$router.push({ path: '/yunwei' });
                        window.localStorage.setItem('admin','yunwei')
                      }else if(this.formLogin.username.trim()=="xingcong"){//单独给市场部的页面展示
                        if(this.formLogin.username.trim()!="xingcong"){
                          self.$Message.success('账号错误!');
                          return
                        }
                        if(this.formLogin.password.trim()!="chuxing0616"){
                          self.$Message.success('密码错误!');
                          return
                        }
                        self.$Message.success('提交成功!');
                        self.$router.push({ path: '/consumer' });
                        window.localStorage.setItem('admin','xingcong')
                      }else if(this.formLogin.username.trim()=="myceshi"){//单独给的页面展示
                        if(this.formLogin.username.trim()!="myceshi"){
                          self.$Message.success('账号错误!');
                          return
                        }
                        if(this.formLogin.password.trim()!="myceshi"){
                          self.$Message.success('密码错误!');
                          return
                        }
                        self.$Message.success('提交成功!');
                        self.$router.push({ path: '/queryPeopleInfo' });
                        window.localStorage.setItem('admin','myceshi')
                      }else if(this.formLogin.username.trim()=="sousoushenbian"){//单独给的页面展示
                        if(this.formLogin.username.trim()!="sousoushenbian"){
                          self.$Message.success('账号错误!');
                          return
                        }
                        if(this.formLogin.password.trim()!="sousoushenbian123"){
                          self.$Message.success('密码错误!');
                          return
                        }
                        self.$Message.success('提交成功!');
                        self.$router.push({ path: '/chart' });
                        window.localStorage.setItem('admin','sousoushenbian')
                      }else{
                        if(this.formLogin.username.trim()!="admin"){
                          self.$Message.success('账号错误!');
                          return
                        }
                        if(this.formLogin.password.trim()!="xapoint!@#123"){
                          self.$Message.success('密码错误!');
                          return
                        }
                        self.$Message.success('提交成功!');
                        self.$router.push({ path: '/consumer' });
                        window.localStorage.setItem('admin','admin')
                      }

                    } else {
                        this.$Message.error('表单验证失败!');
                    }
                     if(this.formLogin.remember[0] == "记住密码"){
                        sessionStorage.setItem('username', JSON.stringify(this.formLogin.username));
                        sessionStorage.setItem('password', JSON.stringify(this.formLogin.password));
                    }else{
                        sessionStorage.removeItem('username');
                        sessionStorage.removeItem('password');
                    }
                })
            },
        },
        mounted() {
          console.log(sessionStorage.getItem('username'))
            if(sessionStorage.getItem('username')){
                this.formLogin.username = JSON.parse(sessionStorage.getItem('username'));
            }
            if(sessionStorage.getItem('password')){
                this.formLogin.password = JSON.parse(sessionStorage.getItem('password'));
            }
        }
    }
</script>


<style scoped>
  .card-box {
    padding: 20px;
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    background-color: #F9FAFC;
    margin: 180px auto;
    width: 400px;
  }
  
  .title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  .formLogin-title {
      text-align: center;
      font-size: 28px;
  }
  .formLogin-title h3{
      font-size: 18px;
  }
  .login-no-bottom {
      	margin-bottom: 10px;
  }
 
</style>



